.Dashboard{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.Dashboard-content{
  color: red;
  width: 75%;
  background: black;
  -webkit-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
.Dashboard aside{
  width: 25%;
  -webkit-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}

.Dashboard-content.active{
  width: 100%;
}
.Dashboard aside.active{
  width:0;
  overflow: hidden;
}




